import { NgModule } from "@angular/core";
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from "./main/main.component";
import { PaneComponent } from "./pane/pane.component";
import { PaneGuard } from "../services/pane-guard.service";
import { UserAllComponent } from "./user-all/user-all.component";
import { UpdatePasswordComponent } from "./update-password/update-password.component";
import { ImeiAllComponent } from "./imei-all/imei-all.component";
import { ImeiUpdateComponent } from "./imei-update/imei-update.component";
import { ImeiFindComponent } from "./imei-find/imei-find.component";
import { ImeiRemoveComponent } from "./imei-remove/imei-remove.component";
import { ImeiAddComponent } from "./imei-add/imei-add.component";

let routes:Routes = [
    {path:'',pathMatch:'full',redirectTo:'main'},
    {path:'main',component:MainComponent},
    {path:'pane',component:PaneComponent,canActivate:[PaneGuard],children:[
        {path:'user/all',component:UserAllComponent},
        {path:'user/update',component:UpdatePasswordComponent},
        {path:"imei/all",component:ImeiAllComponent},
        {path:"imei/update",component:ImeiUpdateComponent},
        {path:"imei/find",component:ImeiFindComponent},
        {path:"imei/remove",component:ImeiRemoveComponent},
        {path:"imei/add",component:ImeiAddComponent}
    ]},
    {path:'**',redirectTo:'main'}
];

@NgModule({
    imports:[
        RouterModule.forRoot(routes)
    ],
    exports:[
        RouterModule
    ]
})
export class AppRoutingModule{ }


